	import common from '../../../common/common.js'
	
	export default {
		data() {
			return {
				uname:'',
				sex:'',
				mobile:'',
				title:'',
				address:'',				
				lat:'',
				lng:'',
				number:'',
				tag:'',	
				id:'',
				userid:'',
				info:{},
				
				titleHeight:0,
				statusBarHeight:0,
				naviBarHeight:0,
			};
		},
		onLoad(options) {
			var that = this
			var id = options.id
			console.log("id",id);
			var userid = uni.getStorageSync('userid');
			that.userid = userid;
			
			var currentPages = getCurrentPages()
			that.info.delta = currentPages.length
			
			that.titleHeight = common.titleHeight;
			that.statusBarHeight = common.statusBarHeight;
			that.naviBarHeight = common.naviBarHeight;
			
			
			if (id > 0) {
			  //修改地址
			  that.id = id
			  
			  that.getAddress(id);
			}
		},
		methods:{
			
			formSubmit(e){
				console.log(e);
				var that = this
				var ua_name = that.uname
				var ua_mobile = that.mobile
				var ua_title = that.title
				var ua_address = that.address
				var ua_lat = that.lat
				var ua_lng = that.lng
				var ua_number = that.number
				var ua_sex = that.sex
				var ua_tag = that.tag
				
				if(ua_name == ''){
					uni.showToast({
						title: '请输入姓名',
						duration: 1000,
						icon :'none'
					});
					return false
				}
				if(ua_mobile == ''){
					uni.showToast({
						title: '请输入电话',
						duration: 1000,
						icon :'none'
					});
					return false
				}
				if(ua_address == '' ){
					uni.showToast({
						title: '请选择地址',
						duration: 1000,
						icon :'none'
					});
					return false
				}
				
				uni.showLoading({
					title: '处理中'
				});
				
				uni.request({
					url: common.apiUrl + 'user/saveAddress',
					method: 'GET',
					data: {
						userid:that.userid,
						id:that.id,
						ua_name:ua_name,
						ua_mobile:ua_mobile,
						ua_title:ua_title,
						ua_address:ua_address,
						ua_lat:ua_lat,
						ua_lng:ua_lng,
						ua_number:ua_number,
						ua_sex:ua_sex,
						ua_tag:ua_tag
					},
					success: res => {
						console.log(res)
						if(res.data.code == 0){
							uni.showToast({
								title: '操作成功',
								duration: 1500,
								icon:'success'
							});
							uni.navigateBack({
								delta: 2
							});
						}else{
							uni.showToast({
								title: res.data.msg,
								duration: 1500,
								icon:'none'
							});
							return false
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//打开地图
			openmap(){
				var that = this
				uni.chooseLocation({
					success: function (res) {
						that.title = res.name
						that.address = res.address
						that.lat = res.latitude
						that.lng = res.longitude
// 						console.log('位置名称：' + res.name);
// 						console.log('详细地址：' + res.address);
// 						console.log('纬度：' + res.latitude);
// 						console.log('经度：' + res.longitude);
					}
				});
			},
			//选择姓别
			setsex(e){
				var that = this;
				that.sex = e.currentTarget.dataset.value ;
			},
			
			//选择标签
			settag(e){
				var that = this;
				that.tag = e.currentTarget.dataset.value ;
			},
			
			//获取地址
			getAddress(id){
				var that = this
				uni.request({
					url: common.apiUrl+'user/getUserAddres',
					method: 'GET',
					data: {
						id:id
					},
					success: res => {
						var data = res.data
						if(data.code == 0){
							that.uname = data.address.ua_name;
							that.sex = data.address.ua_sex;
							that.mobile = data.address.ua_mobile;
							that.title = data.address.ua_title;
							that.address = data.address.ua_address;
							that.lat = data.address.ua_lat;
							that.lng = data.address.ua_lng;
							that.number = data.address.ua_number;
							that.tag = data.address.ua_tag;
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
